import React, {
  lazy,
  Suspense,
  useRef,
  useEffect,
  useState,
  createContext,
} from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import CountryPage from "./components/CountryPage";
import axios from "axios";
import Home from "./components/Home";
import "./tailwind.css";
import "./app.css";

// const Home = lazy(() => import("./components/Home"));
// const CountryPage = lazy(() => import("./components/CountryPage"));

export const allCountriesDataContext = createContext();

const App = (props) => {
  const [allCountries, setAllCountries] = useState();

  useEffect(() => {
    axios
      .get("https://restcountries.eu/rest/v2/all")
      .then((response) => {
        setAllCountries(response.data);
      })
      .catch((error) => console.log(error));
  }, []);

  console.log(allCountries);

  return (
    <Router>
      <allCountriesDataContext.Provider value={allCountries}>
        {/* <Suspense fallback={<p>Hola</p>}> */}
        <div className="global-container">
          <div className="dark:text-white transition duration-500">
            <Switch>
              <Route exact path="/">
                <Home allCountries={allCountries} />
              </Route>
              {/* {allCountries && (
                <Route path="/:countryName">
                  <CountryPage
                    allCountries={allCountries}
                    key={Math.random()}
                  />
                </Route>
              )} */}

              {allCountries &&
                allCountries.map((country) => (
                  <Route path={`/${country.name}`} key={country.name}>
                    <CountryPage countryName={country.name} />
                  </Route>
                ))}

              <Route path="*">
                <div>
                  <h1>This route does not match with anything</h1>
                </div>
              </Route>
            </Switch>
          </div>
        </div>
        {/* </Suspense> */}
      </allCountriesDataContext.Provider>
    </Router>
  );
};

export default App;
